import getShuffledDeck from './rules/getShuffledDeck'
const shuffledDeck = getShuffledDeck()

const testCase = ''
const testDeck = {
  hasAce: ['A'], // A 
  split: ['A', 'A'], //  
  das: [8, 8, 6, 3, 2], // [SP, D]   
  autoStay: [9, 5, 7, 7], // [H] 21   
  dealerBust: ['K', 'K', 'K', '6', 'Q'], // [S]     
  dealerBlackjack: [9, 5, 'A', 7, 'K'], // [H]  21   
  blackjack: ['A', 'K', 6], // []    
  blackjackWin: ['A', 'K', 'A', 2], // []   1    
  blackjackDraw: ['A', 'K', 'K', 'A'], // []   
  blackjackAfterSplit: ['A', 'A', 'A', 'K', 'K', 9], // [SP, H, H]    
  dealerBustAfterBlackjackAfterSplit: ['A', 'A', 'J', 9, 'A', 'Q', 3, 10] // [SP, H, H, H]      
}

export default () =>
  testDeck[testCase]
    ? { deck: [...testDeck[testCase], ...shuffledDeck] }
    : undefined
